*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  font-family: arial, Georgia, 'Times New Roman', Times, 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: rgb(58, 9, 58);
}
.header{
  
  align-items: center;
  padding: 20px;
  background-color: rgb(82, 11, 82);
  color: white;
  z-index: 1;
}
.header>.nav{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
Button{
  padding:5px;
  width: 100px;
  text-align: center;
  color:  rgb(255, 255, 255);
  border-radius: 10px;
  border: none;
  font-weight: 600;
  background-color: rgb(133, 62, 133);
  cursor: pointer;
}
Button:hover{
  border: 2px white solid;
}
.header>.nav>.headers{
  font-size:1.5rem;
}
.section{
  height:80vh;
  display:flex;
  margin:auto;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.section>p{
  color: white;
  max-width: 50%;
  margin: 20px;
  text-align: center;
  line-height:25px;
}
.btns>.btn-1{
  margin-right: 10px;
}
.footer{
  background-color: rgb(41, 6, 41);
  margin: 0px;
  padding:1px;
  z-index: 1;
}
.list-container{
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: white;
  margin: 20px;
  padding: 0px;
}
li{
  list-style: none;
}
.list{
  color: violet;
  font-weight: 800;
}
li:hover{
  cursor: pointer;
  color: violet;
}
.form,.form1{
  display:flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  z-index: 3;
  background-color: white;
  padding:20px;
  border-radius:10px;
  position: relative;
  transform:translateY(-20px)
}

.form>label{
  display: block;
  margin:10px;
}
.form1>label{
  display: block;
  margin:10px;
}
input, textarea{
  display: block;
  padding: 10px;
  min-width: 400px;
}
#submit{
  background-color:black;
}
.visi{
  display:none ;
}
.close,.close1{
  background-color: white;
  height: 25px;
  width: 15px;
  text-align: center;
  transform: translateY(10px);
  z-index: 4;
  position: relative;
  color:red;
  font-weight:700;
  font-size: 20px;
  margin-left: 440px;
}
.btn-2{
  margin-left:120px;
  margin-top:-100px;
}
.btn-1{
  margin-bottom:-100px;
  transform:translateY(17px)
}
.mid{
  text-align: center;
  color: white;
}